
.footer-container {
  background-color: #E4F2F2; /*242424*/
  /*padding: 2rem 0 2rem 0;*/
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fff;
  padding: 15px;
}

h4 {
  color: #262626;
}

.footer-link-items a {
  color: #262626;
}

.footer-link-items:hover {
  color: #578a8a;
  transition: 0.3s ease-out;
}

.website-rights {
  color: #262626;
  margin-bottom: 16px;
}

