  margin-left: 94%;
  bottom: 2em;
  cursor: pointer;
  display: flex;
}*/

.up_arrow {
  box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
  border-radius: 50%;
  padding: 0.3em;
}


.scrollTop {
  position: fixed; 
  width: 100%;
  bottom: 2em;
  /*margin-left: 47%;*/
  margin-right: 4vw;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  cursor: pointer;
  animation: fadeIn 0.3s;
  transition: opacity 0.4s;
}

.scrollTop:hover{
  opacity: 1;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.5;
  }
}


@media screen and (max-width: 820px) {
  .footer-links {
    padding-top: 2rem;
  }

  .footer-link-wrapper {
    flex-direction: column;
  }

  .social-media-wrap {
    flex-direction: column;
  }
}

@media screen and (max-width: 414px) {
  .footer-container {
    justify-content: center;
    font-size: 14px;
  }
}